import ready from '../utilities/_helper';
import renderGrid from '../components/_grid';
var home;
(function (home) {
    let file;
    let index = 0;
    let type;
    function createfolder(btn) {
        btn.onclick = () => {
            type = 'folder';
        };
    }
    home.createfolder = createfolder;
    function createfile(btn) {
        btn.onclick = () => {
            type = 'file';
        };
    }
    home.createfile = createfile;
    function adddata(btn) {
        btn.onclick = function () {
            file = {
                id: index++,
                NameFile: document.querySelector("#txtName").value,
                type: 'folder',
                createBy: 'Vu Nguyen',
                createAt: document.querySelector("#txtModify").value,
                modifiedAt: document.querySelector("#txtModify").value,
                modifiedBy: document.querySelector("#txtModifyBy").value
            };
            file.type = type;
            let data = localStorage.info == null ? [] : JSON.parse(localStorage.info);
            data.push({
                "id": file.id,
                "namefile": file.NameFile,
                "type": file.type,
                "modify": file.modifiedAt,
                "modifyby": file.modifiedBy
            });
            localStorage.info = JSON.stringify(data);
            alert("Created");
            list();
        };
    }
    home.adddata = adddata;
    function list() {
        let data = localStorage.info == null ? [] : JSON.parse(localStorage.info);
        let table = document.querySelector("#tbData");
        table.innerHTML = "";
        data.forEach((element) => {
            if (element.type === 'folder') {
                table.innerHTML +=
                    `
      <div class="row" id="row-2">
        <div class="col-1 space"></div>
          <div class="col-1 icon">
            <img src="https://spoprod-a.akamaihd.net/files/fabric/assets/item-types/20/folder.svg?v6"
                        width="16px" height="16px">
          </div>
          <div class="col-3 name">
            <span class="flex-box" >
              <span class="name-text" onclick="home.edit(${element.id})">${element.namefile}</span>
            </span>
          </div>
          <div class="col-2 modify">
            <span class="flex-box">
              <span>${element.modify}</span>
            </span>
          </div>
          <div class="col-2 modify">
            <span class="flex-box">
              <span>${element.modifyby}</span>
            </span>
          </div>
          <div class="col option">
            <span class="flex-box">
              <span onclick="home.deleterow(${element.id})">Delete</span>
            </span>
          </div>
        </div>
      `;
            }
            else {
                table.innerHTML +=
                    `
      <div class="row" id="row-2">
        <div class="col-1 space"></div>
          <div class="col-1 icon">
            <img src="https://spoprod-a.akamaihd.net/files/fabric/assets/item-types/16_1.5x/docx.svg?v6"
                        width="16px" height="16px">
          </div>
          <div class="col-3 name">
            <span class="flex-box" >
              <span class="name-text" onclick="home.edit(${element.id})">${element.namefile}</span>
            </span>
          </div>
          <div class="col-2 modify">
            <span class="flex-box">
              <span>${element.modify}</span>
            </span>
          </div>
          <div class="col-2 modify">
            <span class="flex-box">
              <span>${element.modifyby}</span>
            </span>
          </div>
          <div class="col option">
            <span class="flex-box">
              <span onclick="home.deleterow(${element.id})">Delete</span>
            </span>
          </div>
        </div>
      `;
            }
        });
    }
    home.list = list;
    function edit(doc) {
        let data = localStorage.info == null ? [] : JSON.parse(localStorage.info);
        let namefile = document.querySelector("#txtName");
        let modifiedAt = document.querySelector("#txtModify");
        let modifiedBy = document.querySelector("#txtModifyBy");
        let btnCreate = document.querySelector("#btnCreate");
        let btnEdit = document.querySelector("#btnEdit");
        let result = data.find((e) => e.id === doc);
        if (result != undefined) {
            btnCreate.style.display = "none";
            btnEdit.style.display = "block";
            namefile.value = result.namefile;
            modifiedAt.value = result.modify;
            modifiedBy.value = result.modifyby;
        }
        else {
            console.log('hi');
            alert("No items found");
        }
        let editButton = document.getElementById("btnEdit");
        editButton.onclick = () => {
            let data = localStorage.info == null ? [] : JSON.parse(localStorage.info);
            let Namfile = document.querySelector("#txtName").value;
            let ModifyAt = document.querySelector("#txtModify").value;
            let ModifiedBy = document.querySelector("#txtModifyBy").value;
            let btnCreate = document.querySelector("#btnCreate");
            let btnEdit = document.querySelector("#btnEdit");
            let id = data.find((e) => e.id == doc).id;
            console.log(id);
            data[id].namefile = Namfile;
            data[id].modify = ModifyAt;
            data[id].modifyby = ModifiedBy;
            localStorage.info = JSON.stringify(data);
            btnCreate.style.display = "block";
            btnEdit.style.display = "none";
            list();
            alert("Modified");
        };
    }
    home.edit = edit;
    function deleterow(id) {
        let data = localStorage.info == null ? [] : JSON.parse(localStorage.info);
        let result = data.findIndex((e) => e.id == id);
        console.log(result);
        if (result != -1) {
            data.splice(result, 1);
            localStorage.info = JSON.stringify(data);
            list();
        }
        else {
            alert("No items deleted!");
        }
    }
    home.deleterow = deleterow;
})(home || (home = {}));
window.home = home;
ready(() => {
    renderGrid();
    let createFolder = document.getElementById("btnCreateFolder");
    let createFile = document.getElementById("btnCreateFile");
    let createButton = document.getElementById("btnCreate");
    home.adddata(createButton);
    home.list();
    home.createfolder(createFolder);
    home.createfile(createFile);
});
